<script>
import logo from "./assets/logo.webp";
import Testimonials from "./components/Testimonials.vue";
import Swal from "sweetalert2";
import { SpeedInsights } from "@vercel/speed-insights/vue";

export default {
  name: "LandingPage",
  data() {
    return {
      logo,
      isMenuOpen: false, // √©tat menu burger
      contact: { name: "", email: "", message: "" },
      showPopup: false,
      showDemoModal: false,
      showLoginInfo: false,
      selectedDemo: "",
      blogPosts: [
        {
          excerpt: "Discover the latest strategies and tools...",
          image: "/images/blog1.png",
          link: "/blog/gerer-son-stock-2026.html",
        },
        {
          title: "5 Common Inventory Management Mistakes",
          excerpt: "These mistakes hinder growth...",
          image: "/images/blog2.png",
          link: "/blog/erreurs-gestion-inventaire.html",
        },
        {
          excerpt: "Discover FlowStock, the free software...",
          image: "/images/blog3.png",
          link: "/blog/comparatif-logiciels-stock.html",
        },
      ],
    };
  },
  components: { Testimonials, SpeedInsights },
  methods: {
    toggleMenu() {
      this.isMenuOpen = !this.isMenuOpen;
    },
    closeMenu() {
      this.isMenuOpen = false;
    },
    scrollToSection(id) {
      const section = document.getElementById(id);
      if (section) {
        section.scrollIntoView({ behavior: "smooth", block: "start" });
      }
    },
    openDemo(videoFile) {
      this.selectedDemo = `/${videoFile}`; // chemin depuis la racine
      this.showDemoModal = true;
    },
    goToTrial() {
      this.showPopup = true;
    },
    closePopup() {
      this.showPopup = false;
      setTimeout(() => (this.showLoginInfo = true), 300);
    },
    closeLoginInfo() {
      this.showLoginInfo = false;
      const exeUrl =
        "https://drive.google.com/uc?export=download&id=14KRzfWq899xRogBghqUk1MJMz5__icM4";
      window.open(exeUrl, "_blank");
    },
    closeDemo() {
      this.showDemoModal = false;
      this.selectedDemo = "";
    },
    async submitForm() {
      if (!this.contact.name || !this.contact.email || !this.contact.message) {
        await Swal.fire({
          icon: "warning",
          title: "Champs manquants",
          text: "Merci de remplir tous les champs avant d‚Äôenvoyer.",
          confirmButtonColor: "#96154a",
        });
        return;
      }

      const mail = "flowstockgs@gmail.com"; // üî• Mets ton email ici !

      const subject = encodeURIComponent("Nouveau message depuis FlowStock");
      const body = encodeURIComponent(
        `Nom : ${this.contact.name}\nEmail : ${this.contact.email}\nMessage :\n${this.contact.message}`
      );

      // Ouvre l‚Äôemail
      window.location.href = `mailto:${mail}?subject=${subject}&body=${body}`;

      await Swal.fire({
        icon: "success",
        title: "Message pr√™t √† √™tre envoy√© !",
        text: "Votre logiciel email va s‚Äôouvrir.",
        confirmButtonColor: "#96154a",
      });

      this.contact = { name: "", email: "", message: "" };
    },

    // üîπ scrollToSection doit √™tre ici, dans methods
    scrollToSection(id) {
      const section = document.getElementById(id);
      if (section) {
        section.scrollIntoView({
          behavior: "smooth",
          block: "start",
        });
      }
    },
  },
  mounted() {
    // Emp√™che le menu contextuel et Ctrl+U (source) ‚Äî dissuasif seulement
    this._preventContext = (e) => e.preventDefault();
    this._preventKeys = (e) => {
      // D√©sactive Ctrl+U, Ctrl+Shift+I, F12 (devtools) et Ctrl+S / Ctrl+P si souhait√©
      if (e.ctrlKey && (e.key === "u" || e.key === "U")) {
        e.preventDefault();
      }
      if (e.ctrlKey && e.shiftKey && (e.key === "I" || e.key === "i")) {
        e.preventDefault();
      }
      if (e.key === "F12") {
        e.preventDefault();
      }
    };

    document.addEventListener("contextmenu", this._preventContext);
    document.addEventListener("keydown", this._preventKeys);
  },
  beforeUnmount() {
    document.removeEventListener("contextmenu", this._preventContext);
    document.removeEventListener("keydown", this._preventKeys);
  },
};
</script>

