  border-left: 4px solid #96154a; /* accent couleur FlowStock */
  border-radius: 5px;
  margin-top: 10px;
  line-height: 1.4;
}
/* Bouton burger */
.burger {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 25px;
  height: 20px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}

.burger span {
  display: block;
  height: 3px;
  width: 100%;
  background-color: #b03a2e;
  border-radius: 2px;
  transition: all 0.3s ease;
}

/* Animation burger */
.burger span.burger-open:nth-child(1) {
  transform: rotate(45deg) translateY(8px);
}
.burger span.burger-open:nth-child(2) {
  opacity: 0;
}
.burger span.burger-open:nth-child(3) {
  transform: rotate(-45deg) translateY(-8px);
}

